import { type ReactElement, useState } from 'react'
import styles from './SortBy.module.css'
import { useDispatch } from 'react-redux'
import { isDisplay } from '../../store/slice/sortbySearchSlice'

interface SortByType {
  setCurrentSort: (p: string) => void
  setPage: (p: number) => void
  isDisplayVal: boolean
}
const SortBy = ({ setCurrentSort, setPage, isDisplayVal }: SortByType): ReactElement => {
  const dispatch = useDispatch()

  // sortBy       ,   
  const paginationDisplayHandler = (): void => {
    // eslint-disable-next-line @typescript-eslint/no-unused-expressions
    !isDisplayVal ? dispatch(isDisplay(true)) : null
  }

  // sort by  select
  const [optionText] = useState([
    '====',
    '',
    '',
    '',
    '',
    ' ',
    ' ',
    ' ',
    ' '
  ])
  const [optionVal] = useState([
    null,
    'popularity.desc',
    'popularity.asc',
    'release_date.desc',
    'release_date.asc',
    'revenue.desc',
    'revenue.asc',
    'vote_average.desc',
    'vote_average.asc'
  ])

  return (
    <article className={styles.sort_by}>
      <div className={styles.sort_by_inner_con}>
        {' '}
        <h3 className={styles.sort_by_title}></h3>
        {/* select     */}
        <select
          onChange={(e) => {
            //  sortBy  (, )  sortBy    
            //   1 .
            setPage(1)
            // sortBy         .
            setCurrentSort(e.target.value)

            //  /  
            paginationDisplayHandler()
          }}
          className={styles.sort_by_select}
        >
          {optionText.map((text, i) => {
            return (
              <option key={text} value={`${optionVal[i]}`}>
                {text}
              </option>
            )
          })}
        </select>
      </div>
    </article>
  )
}

export default SortBy
