import { createSlice, createAsyncThunk, type PayloadAction } from '@reduxjs/toolkit'
import axios from 'axios'
import { API_KEY } from '../../pages/HomePage'

// axios  (         .)
export const baseSet = axios.create({
  baseURL: 'https://api.themoviedb.org',
  headers: { 'content-type': 'application/json;charset=utf-8' }
})

const getMovieData = createAsyncThunk('GET/movieData', async () => {
  //  
  const popular = baseSet
    .get(
      `/3/discover/movie?api_key=${API_KEY}&language=ko-KR&sort_by=popularity.desc&include_adult=false&include_video=false&page=1&with_watch_monetization_types=flatrate`
    )
    .then((res) => {
      return res.data
    })
    .catch((error) => {
      console.error('popular:', error)
    })

  //   
  const topRate = baseSet
    .get(`/3/movie/top_rated?api_key=${API_KEY}&language=ko-KR&page=1`)
    .then((res) => {
      return res.data
    })
    .catch((error) => {
      console.error('topRate:', error)
    })

  //  
  const latest = baseSet
    .get(`/3/movie/latest?api_key=${API_KEY}&language=ko-KR&page=1`)
    .then((res) => {
      return res.data
    })
    .catch((error) => {
      console.error('last:', error)
    })

  //   
  const isComing = baseSet
    .get(`/3/movie/upcoming?api_key=${API_KEY}&language=ko-KR&page=1`)
    .then((res) => {
      return res.data
    })
    .catch((error) => {
      console.error('isComing:', error)
    })

  //   
  const genreData = baseSet
    .get(`/3/genre/movie/list?api_key=${API_KEY}&language=ko-KR`)
    .then((res) => {
      return res.data
    })
    .catch((error) => {
      console.error('genreData:', error)
    })

  const [popularMovie, topRateMovie, isComingMovie, genreInfo, latestMovie] =
    await Promise.all([popular, topRate, isComing, genreData, latest])
  return { popularMovie, topRateMovie, isComingMovie, genreInfo, latestMovie }
})

// ================================================================

//  .
const movieSlice = createSlice({
  name: 'movieData',
  initialState: {
    data: '',
    status: 'WelCome'
  },
  // reducers     
  reducers: {},

  // extraReducers     
  //  , ,             .
  extraReducers (builder) {
    builder.addCase(getMovieData.pending, (state) => {
      state.status = 'Loading..'
    })
    builder.addCase(
      getMovieData.fulfilled,
      (state, action: PayloadAction<any>) => {
        state.data = action.payload
        state.status = 'Complete!'
      }
    )
  }
})

export default movieSlice.reducer
export { getMovieData }
